<template>
  <div class="map-notify">
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'add-station'"
      title="[]"
      type="success"
      :closable="false"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'edit-station'"
      title="[]"
      type="success"
      :closable="false"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'remove-station'"
      title="[]"
      type="error"
      :closable="false"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'add-path' && EditorOption.AddPathMode.Direction == 'one-direction'"
      title="[]"
      type="success"
      :closable="false"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'add-path' && EditorOption.AddPathMode.Direction == 'bi-direction'"
      title="[]"
      type="success"
      :closable="false"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'edit-path'"
      title="[]"
      type="success"
      :closable="false"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'remove-path'"
      title="[]"
      type="error"
      :closable="false"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'add-forbid-region'"
      :title="`[/][${EditorOption.AddRegionMode.Mode == 'forbid' ? '' : ''}]([ESC])`"
      type="success"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'edit-forbid-region'"
      title="[])"
      type="success"
    />
    <el-alert
      class="notify-text"
      v-if="EditorOption.EditAction == 'remove-forbid-region'"
      title="[]"
      type="error"
    />
    <el-alert
      class="notify-text"
      v-if="DragBackgroundImageMode"
      title="[]"
      type="error"
    />
  </div>
</template>

<script>
export default {
  props: {
    DragBackgroundImageMode: {
      type: Boolean,
      default: false
    },
    EditorOption: {
      type: Object,
      default() {
        return {
          // EditMode: 'view',
          EditMode: 'edit',
          EditAction: 'none',
          AddPathMode: {
            Direction: 'one-direction'
          },
          AddRegionMode: {
            Mode: 'forbid' //forbid;passible
          }
        }
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.map-notify {
  .notify-text {
    :deep(.el-alert__title) {
      font-size: 20px;
      letter-spacing: 2px;
    }
  }
}
</style>