<template>
  <el-dialog draggable v-model="visible" :modal="false" @close="updateVisible(false)">
    <div class="d-flex py-3">
      <h3 class="flex-fill text-start">
        <b></b>
      </h3>
      <el-button size="large" type="primary" @click="confirm"></el-button>
    </div>
    <el-table ref="table" :data="eq_store" border :height="800">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="Name" label="Name"></el-table-column>
      <el-table-column prop="TagID" label="Tag"></el-table-column>
    </el-table>
  </el-dialog>
</template>
<script>
import { EqStore } from '@/store';
export default {
  props: {
    value: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      visible: this.value,
    }
  },
  computed: {
    eq_store() {
      return EqStore.state.EqOptions;
    }
  },
  watch: {
    value(newValue) {
      this.visible = newValue;  //  value 

    },
    visible(newValue) {
      this.$emit('input', newValue);  //  visible 
    }
  },
  methods: {
    updateVisible(val) {
      this.visible = val;  //  visible
      if (!val) {
        this.cancelSelectAll();
      }
    },
    selectAll() {
      this.$refs.table.clearSelection();  // 
      this.eq_store.forEach(row => {
        this.$refs.table.toggleRowSelection(row, true);  // 
      });
    },
    cancelSelectAll() {
      this.$refs.table.clearSelection();  // 
    },
    confirm() {
      this.$emit('confirm', this.$refs.table.getSelectionRows());
    }
  }
}
</script>
